import React from "react";
import { RadialChart } from "react-vis";
import "react-vis/dist/style.css";

function PieChart() {
  //   ( )
  const data = [
    { angle: 1, label: "A" },
    { angle: 2, label: "B" },
    { angle: 3, label: "C" },
  ];

  return (
    <div style={{ textAlign: "center" }}>
      <h2>Pie Chart Example</h2>
      <RadialChart
        data={data}
        width={300}
        height={300}
        showLabels={true} //   
      />
    </div>
  );
}

export default PieChart;
